@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');

* {
	font-family: "Roboto Mono", monospace;
}

body {
	height: 100vh;
	width: 100%;
	margin: 0;
}

ol, ul {
	padding-left: 0;
	list-style: none;
	overflow-y: auto;
	height: 30vh;
}

ol li {
	margin-bottom: 1vmin;
}

.game-info button {
	border: 1px solid black;
	background: 0;
	cursor: pointer;
	padding: 1vmin;}

.game-info button:hover {
	color: red;
	border-color: red;
}

ol button:focus {
	font-weight: bold;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.buttons {
	display: flex;
	flex-direction: column;
}

.buttons button {
	margin-bottom: 1vmin;
}


.status {
	margin: 2vmin;
	text-align: center;
	text-transform: uppercase;
}

.square {
	background: #fff;
	border: 1px solid black;
	float: left;
	font-size: 6vmin;
	font-weight: bold;
	line-height: 10vmin;
	height: 10vmin;
	width: 10vmin;
	margin-right: 0;
	margin-top: 0;
	padding: 0;
	text-align: center;
}

.square:focus {
	outline: none;
}

.kbd-navigation .square:focus {
	background: #ddd;
}

#root {
	height: 100%;
}

.game {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100%;
}

.game-board {
	padding: 6vmin;
}

.game-info {
	margin-left: 0;
}

.game-info button {
	min-width: 30vmin;
}